import { useState } from 'react';
import './style.scss';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

library.add(faTimes)

const Navbar = () => {
    const [isSideNav, setIsSideNav] = useState(false);
    const resumelink = 'https://drive.google.com/file/d/1cZla22sHuIDvE0FS_0vePlZ60klcJLAd/view?usp=sharing';

    const sideNavClick = () => {
        setIsSideNav(false);
    }

    return (
        <nav className="navbar navbar-expand-lg navbar-light px-4 px-lg-5 fixed-top">
            <div className="container">
                <a className="navbar-brand font-weight-bolder" href="#">AburizalAN</a>
                <button onClick={() => setIsSideNav(isSideNav ? false : true)} className="navbar-toggler d-flex d-lg-none" type="button" data-toggle="collapse" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    {isSideNav ? <FontAwesomeIcon icon={faTimes} /> : <span className="navbar-toggler-icon"></span>}
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul id="menu" className="navbar-nav ml-auto">
                        <li className="nav-item"><a className="nav-link" href="#header">Home</a></li>
                        <li className="nav-item"><a className="nav-link" href="#about">About</a></li>
                        <li className="nav-item"><a className="nav-link" href="#journey">Journey</a></li>
                        <li className="nav-item"><a className="nav-link" href="#portofolio">Portofolio</a></li>
                        <li className="nav-item"><a className="nav-link" href="#contact">Get in Touch</a></li>
                    </ul>
                    
                    <a className="myresume ml-0 ml-lg-4 text-white text-decoration-none" href={resumelink}>
                        My Resume
                    </a>
                </div>

                <div className={`sideNav bg-light position-fixed ${isSideNav ? 'active' : 'disabled'}`}>
                    <ul id="menu" className="navbar-nav">
                        <li onClick={sideNavClick} className="nav-item"><a className="nav-link" href="#header">Home</a></li>
                        <li onClick={sideNavClick} className="nav-item"><a className="nav-link" href="#about">About</a></li>
                        <li onClick={sideNavClick} className="nav-item"><a className="nav-link" href="#journey">Journey</a></li>
                        <li onClick={sideNavClick} className="nav-item"><a className="nav-link" href="#portofolio">Portofolio</a></li>
                        <li onClick={sideNavClick} className="nav-item"><a className="nav-link" href="#contact">Get in Touch</a></li>
                    </ul>
                    
                    <a className="myresume ml-0 ml-lg-4 text-white text-decoration-none" href={resumelink}>
                        My Resume
                    </a>
                </div>
            </div>
        </nav>
    )
}

export default Navbar;