import './style.scss';
import HeaderIllustration from '../../img/header-illustration.png';

const Header = () => {
    return (
        <div className="d-flex flex-column" style={{minHeight: '768px'}}>
            <div style={{height: '80px', display: 'block'}}></div>
            <section className="header" style={{flex: 1}} id="header">
                <div className="container h-100">
                    <div className="row h-100 align-items-center">
                        <div className="col-lg-5">
                            <h1 className="big-title">
                                Hello World, <br/> My Name is 
                            </h1>
                            <h1 className="big-title bg-shape-purple mb-3">Aburizal Adi</h1>
                            <h1 className="big-title bg-shape-purple mb-3">Nugroho</h1>
                            <div>
                                <h5 className="mt-5" style={{fontWeight: 800}}>
                                    Front End Developer
                                </h5>
                                <div className="line"></div>
                            </div>
                        </div>
                        <div className="col-lg-7">
                            <img className="ml-lg-auto mx-auto d-block" src={HeaderIllustration} alt="header illustration"/>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Header;